import React from "react";
import logo from "./RUTA.png";
import styled from "styled-components";
import moment from "moment";
import {color} from "./constants";

import {Map, GoogleApiWrapper, Marker} from "google-maps-react";

import {
   Button,
   Divider as HR,
   Typography,
   TextField,
   IconButton,
} from "@material-ui/core";

const Page = styled.div`
   height: 100%;
   width: 100%;
   display: flex;
   flex-flow: column nowrap;
   align-items: center;
   overflow: visible;
   postion: relative;
`;

const Card = styled.div`
   display: flex;
   flex-flow: row nowrap;
   width: 100%;
   height: 150px;
   position: absolute;
   bottom: 0;
   left: 0;
   background: white;
   justify-content: space-between;
   align-items: center;
`;

const CardItem = styled.div`
   flex: 1;
   display: flex;
   flex-flow: column nowrap;
   padding: 24px;
`;
const StyledMap = styled(Map)``;

const GoButton = styled(Button)`
   color: white;
   background: ${color.green};
   &:hover {
      background: ${color.green1};
   }
`;
function MapScreen({data, google}) {
   const cards = [
      {
         time: "17 min",
         distance: "5.4 mi",
         path: `${data.start.address} -> ${data.stops
            .map(stop => stop.address)
            .join(" -> ")} -> ${data.end.address}`,
         link: `https://www.google.com/maps/dir/?api=1&origin=${encodeURI(
            data.start.address,
         )}&destination=${encodeURI(data.end.address)}&waypoints=${encodeURI(
            data.stops.map(stop => stop.address).join("|"),
         )}`,
         coords: data.start.coords,
      },
   ];

   return (
      <Page>
         <StyledMap
            google={google}
            initialCenter={{
               lat: 35.257296,
               lng: -120.687276,
            }}>
            <Marker
               name={data.start.address}
               position={{
                  lat: data.start.coords.lat,
                  lng: data.start.coords.lng,
               }}
            />
            {data.stops.map(stop => (
               <Marker
                  name={stop.address}
                  position={{lat: stop.coords.lat, lng: stop.coords.lng}}
               />
            ))}
            <Marker
               name={data.end.address}
               position={{lat: data.end.coords.lat, lng: data.end.coords.lng}}
            />
         </StyledMap>
         {cards.map(card => (
            <Card key={card}>
               <CardItem>
                  <Typography variant="h5" color="primary">
                     {card.time}
                  </Typography>
                  <Typography color="secondary">{card.distance}</Typography>
                  <Typography>{card.path}</Typography>
               </CardItem>
               <CardItem>
                  <GoButton component="a" href={card.link}>
                     GO
                  </GoButton>
               </CardItem>
            </Card>
         ))}
      </Page>
   );
}

export default GoogleApiWrapper({
   apiKey: "AIzaSyA8O9T0kXIML1T2y9F0UorRWZ-P43zCSmc",
})(MapScreen);
